/* 
  Main Color Scheme
  Dark Blue: #1F3044
  Orange: #FB9039
  Gray #646C79
*/

.container {
  display: flex;
  flex-flow: row-reverse wrap-reverse;
  justify-content: space-evenly;
  width: 90%;
  margin: auto;
  margin-top: 100px;
}
.header {
  background-color: #fb9039;
  flex-shrink: 3;
  width: 20%;
  min-width: 250px;
  max-width: 300px;
  /* background-repeat: y; */
  padding: 20px;
  box-shadow: 5px 5px 10px black;
  margin-bottom: 20px;
}
.content {
  width: 60%;
  min-width: 100px;
  margin-top: 100px;
  margin-right: 50px;
  flex-grow: 1;
}
.content h1 {
  font-size: 50px;
  color: #fb9039;
}
.content p {
  font-size: 20pt;
  line-height: 70px;
}

@media only screen and (max-width: 500px) {
  .header {
    max-width: 200px;
    padding: 20px;
  }
  .container {
    width: 80%;
    justify-content: space-evenly;
  }
  .content {
    margin: 20px 0px 10px 0px;
  }
  .content h1 {
    font-size: 20pt;
  }
  .content p {
    font-size: 14pt;
    line-height: 30px;
  }
}
@media only screen and (max-width: 950px) {
  .header {
    margin-top: 20px;
    width: 100%;
    max-width: 500px;
  }
}
