body {
  margin: 0;
  font-family: Verdana, Geneva, Tahoma, sans-serif;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navbar {
  width: 100%;
  margin: 0px 0px 40px 0px;
  z-index: 999;
  min-height: 50px;
  padding: 10px;
  position: fixed;
  top: 0px;
  left: 0px;
}
