.avatar {
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  text-align: center;
  margin-bottom: 0px;
}
.avatar-image img {
  width: 100%;
  display: block;
  max-width: 400px;
  background-color: #1f3044;
  background-repeat: x;
}
.avatar-info {
  font-size: 20pt;
  text-align: right;
  line-height: 50px;
  color: #1f3044;
  vertical-align: bottom;
  margin-top: 20px;
}
@media only screen and (max-width: 950px) {
  .avatar {
    display: flex;
    flex-flow: row-reverse;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    text-align: center;
    margin-bottom: 0px;
  }
  .avatar-image img {
    width: 100%;
  }
  .avatar-image {
    margin: 20px;
  }
  .avatar-info {
    margin: 20px;
  }
  @media only screen and (max-width: 725px) {
    .avatar-info {
      font-size: 4vw;
    }
  }
}
@media only screen and (max-width: 500px) {
  .avatar {
    display: flex;
    flex-flow: column wrap-reverse;
    justify-content: space-evenly;
    align-items: center;
    width: 100%;
    text-align: center;
    margin-bottom: 0px;
  }

  .avatar-image img {
    width: 100%;
  }
  .avatar-info {
    font-size: 18pt;
    text-align: right;
  }
}
